import React, { useState } from 'react';
import { HashRouter as Router, Route, Routes, useLocation } from 'react-router-dom';
import './App.css';
import ForceGraph2D from 'react-force-graph-2d';

class CypherViz extends React.Component {
  constructor({ driver }) {
    super();
    this.driver = driver;
    
    this.defaultData = {
      nodes: [],
      links: []
    };
    
    this.state = {
      data: this.defaultData,
      query: `MATCH (u:User)-[r:CONNECTED_TO]->(v:User) RETURN u.name AS source, v.name AS target`,
      debugMessage: ""
    };
  }

  setDebugMessage = (message) => {
    this.setState({ debugMessage: message });
  };

  loadData = async () => {
    let session = this.driver.session({ database: "neo4j" });
    let res = await session.run(this.state.query);
    session.close();

    let nodes = new Set();
    let links = res.records.map(r => {
      let source = r.get("source");
      let target = r.get("target");
      nodes.add(source);
      nodes.add(target);
      return { source, target };
    });

    nodes = Array.from(nodes).map(name => ({ name, x: Math.random() * 500, y: Math.random() * 500 }));
    const updatedData = { nodes, links };
    localStorage.setItem('graphData', JSON.stringify(updatedData));
    this.setState({ data: updatedData });
  };

  componentDidMount() {
    this.loadData();
  }

  addNodeNFC = async (newUser) => {
    this.setDebugMessage(`Adding user ${newUser} to Neo4j...`);
    
    let session = this.driver.session({ database: "neo4j" });
    try {
      await session.run(
        "MERGE (u:User {name: $user}) MERGE (prev:User {name: $prevUser}) MERGE (u)-[:CONNECTED_TO]->(prev)",
        { user: newUser, prevUser: "Root" }
      );
      this.setDebugMessage(`User ${newUser} added successfully!`);
      await this.loadData();
    } catch (error) {
      this.setDebugMessage(`Error adding user: ${error.message}`);
      console.error("Error adding user:", error);
    }
    session.close();
  };

  render() {
    return (
      <Router>
        <div>
          {/* Debug Panel */}
          <div style={{ padding: "10px", background: "#fff4c2", textAlign: "center", borderBottom: "2px solid black" }}>
            <strong>Debug:</strong> {this.state.debugMessage}
          </div>
          
          <Routes>
            <Route path="/NFC" element={<NFCTrigger addNode={this.addNodeNFC} setDebugMessage={this.setDebugMessage} />} />
            <Route path="/" element={<GraphView data={this.state.data} setDebugMessage={this.setDebugMessage} />} />
          </Routes>
        </div>
      </Router>
    );
  }
}

const NFCTrigger = ({ addNode, setDebugMessage }) => {
  const location = useLocation();

  React.useEffect(() => {
    const addAndRedirect = async () => {
      setDebugMessage("NFC tap detected! Generating new user...");
      const newUser = `User-${Date.now()}`;
      
      try {
        await addNode(newUser);
        setDebugMessage(`User ${newUser} added successfully! Redirecting...`);
      } catch (error) {
        setDebugMessage(`Error adding user: ${error.message}`);
        console.error("Error adding user:", error);
        return;
      }

      setTimeout(() => {
        setDebugMessage("Forcing hard redirect due to mobile issue...");
        window.location.assign("/craft-network/#/");
      }, 1000);
    };

    addAndRedirect();
  }, [location]);

  return <div style={{ textAlign: "center", padding: "20px", fontSize: "16px", color: "red" }}>Processing NFC tap...</div>;
};

const GraphView = ({ data, setDebugMessage }) => (
  <div width="95%">
    <ForceGraph2D
      graphData={data}
      nodeId="name"
      nodeLabel="name"
      nodeCanvasObject={(node, ctx) => {
        ctx.fillStyle = "blue";
        ctx.beginPath();
        ctx.arc(node.x || Math.random() * 500, node.y || Math.random() * 500, 6, 0, 2 * Math.PI);
        ctx.fill();
        ctx.fillStyle = "black";
        ctx.fillText(node.name, node.x + 10, node.y);
      }}
      linkDirectionalArrowRelPos={1}
      linkDirectionalArrowLength={5}
    />
  </div>
);

export default CypherViz;
